.example-enter {
  -webkit-transition-delay: 1s;
       -o-transition-delay: 1s;
          transition-delay: 1s;
  visibility: hidden;
  opacity: 0;
  height: 0px;
  width: 0px; }

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition-delay: 1s;
       -o-transition-delay: 1s;
          transition-delay: 1s;
  visibility: visible;
  height: auto;
  width: auto;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in; }

.example-leave {
  opacity: 1; }

.example-leave.example-leave-active {
  opacity: 0;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in; }

.switch-enter {
  opacity: 0.01; }

.switch-enter.switch-enter-active {
  opacity: 1.0;
  -webkit-transition: opacity 0.5s ease-in;
  -o-transition: opacity 0.5s ease-in;
  transition: opacity 0.5s ease-in; }

.switch-leave {
  /* Completely hide the button while it's being animated and before it's removed from the DOM. */
  visibility: hidden;
  height: 0px;
  width: 0px;
  position: absolute;
  /* Starting opacity */
  opacity: 1.0; }

.switch-leave.switch-leave-active {
  /* Ending opacity: 
    Trigger opacity change so the "transitionend" event will fire, causing React to remove from the DOM. */
  opacity: 0;
  -webkit-transition: opacity 0.5s ease-in;
  -o-transition: opacity 0.5s ease-in;
  transition: opacity 0.5s ease-in; }

/* Transition */
.slide-up-appear, .slide-up-enter {
  opacity: 0;
  -webkit-transform: translateY(15px);
      -ms-transform: translateY(15px);
          transform: translateY(15px);
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease; }

